button {
  margin-top: 2rem;
  margin-bottom: 2rem;
  padding: 22px 40px;
  border-radius: 80px;
  border: none;
  color: white;
  background: $asparagus;
  cursor: pointer;
  h4 {
    margin: 0 !important;
    padding: 0 !important;
  }
  &.button--orange {
    background: $primary-orange;
  }
}

.atomic--svg img {
  box-shadow: none !important;
  max-width: 40px;
  min-width: 30px;
}

.img--xs img{
  width: 100%;
  max-width: 400px;
}

.pad--bottom {

  h1, h2, h3, h4, h5, h6 {
    margin-bottom: 2rem !important;
  }
  img {
    margin-bottom: 1rem !important;
  }
}
