@import "colors";
@import "atomics";

h1 {
  font-size: 3.2rem;
  font-family: "Open Sans ExtraBold", sans-serif;
  color: $main-text;
}

h2 {
  font-size: 2.5rem;
  font-family: "Open Sans ExtraBold", sans-serif;
  color:$dark-green;
  padding-bottom: 2rem;
}

h3 {
  font-size: 1.8rem;
  font-family: "Open Sans Bold", sans-serif;
  color:$dark-gray;
  padding-bottom: 1.5rem;
}

h4 {
  font-size: 1.2rem;
  font-family: "Open Sans SemiBold", sans-serif;
  text-transform: uppercase;
  padding-bottom: 0.5rem;
}

h5 {
  font-family: "Open Sans Bold", sans-serif;
  font-size: 1.1rem;
}
h6 {
  font-family: "Open Sans Regular", sans-serif;
  font-size: 1.2rem;
}

p {
  font-size: 1.2rem;
  font-family: "Open Sans Regular", sans-serif;
}

li {
  font-size: 1.15rem;
  font-family: "Open Sans Light", sans-serif;
}

b {
  font-weight: 800;
}

.break{
  position: absolute;
  left: 0;
  //margin-top: 13em;
  top: 70%;
  width: 100px;
  margin-left: 15px;
  height: 5px;
  background: $default-text;
}

.no-shadow img {
  box-shadow: none !important;
  width: 100%;
}

@media screen and (max-width: 767px) {
  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  h3 {
    font-size: 1.2rem;
    line-height: 2rem;
    padding-bottom: 15px;
  }

  h4 {
    //font-size: 1rem;
    font-weight: 600;
  }

  h5{
    font-size: 0.8rem;
    font-weight: 600;
    color: $highlight;
  }

  .break{
    display: none;
  }
}

.footnote {
  padding-top: 10px;
  opacity: 0.8;
  font-size: 0.8rem;
}

/*------------
 *  tooltip
 *------------*/

.tooltips {
  border-bottom: 1px dotted #000;
  cursor: help;
  position: relative;
}

.tooltips-text {
  background: rgba(0,0,0,0.8);
  color: #fff;
  border-radius: 8px;
  bottom: 100%;
  left: 30%;
  margin-left: -20px;
  opacity: 0;
  padding: .5em;
  position: absolute;
  text-align: center;
  transition: opacity .2s;
  visibility: hidden;
  width: 120px;
}

.tooltips-text::after {
  border-color: #000 transparent transparent transparent;
  border-style: solid;
  border-width: 5px;
  content: '';
  left: 50%;
  margin-left: -5px;
  position: absolute;
  top: 100%;
}

.tooltips:hover +.tooltips-text {
  opacity: 1;
  visibility: visible;
}
